import { GlobalProvider } from './../../providers/global/global';
import { RestProvider } from './../../providers/rest/rest';
import { LoadingProvider } from './../../providers/loading/loading';
import { Component, ViewChild } from '@angular/core';
import { NavController, IonicPage, AlertController } from 'ionic-angular';

import { registerPlugin } from 'ngx-filepond';
import FilePondPluginImagePreview from 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.esm';
import FilePondPluginImageExifOrientation from 'filepond-plugin-image-exif-orientation/dist/filepond-plugin-image-exif-orientation.esm';
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type/dist/filepond-plugin-file-validate-type.esm';

registerPlugin(
	FilePondPluginImagePreview,
	FilePondPluginImageExifOrientation,
	FilePondPluginFileValidateType
);

/**
 * Generated class for the ColorMixPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-color-mix',
  templateUrl: 'color-mix.html',
})
export class ColorMixPage {

  @ViewChild('myPond2') myPond: any;
  name: string;
  content: string;
  timestamp: number;
  pondFiles: number;

  pondOptions = {
    class: 'my-filepond',
    allowMultiple: true,
    labelIdle: 'Chọn ảnh',
    labelFileWaitingForSize: 'Đang đợi kích cỡ ảnh',
    labelFileSizeNotAvailable: 'Kích cỡ ảnh không khả dụng',
    labelFileLoading: 'Đang tải',
    labelFileLoadError: 'Lỗi phát sinh khi tải ảnh',
    labelFileProcessing: 'Đang tải lên',
    labelFileProcessingComplete: 'Tải lên thành công',
    labelFileProcessingAborted: 'Đã hủy tải lên',
    labelFileProcessingError: 'Lỗi khi tải ảnh lên',
    labelTapToCancel: 'Hủy',
    labelTapToRetry: 'Thử lại',
    labelTapToUndo: 'Xóa ảnh',
    labelButtonRemoveItem: 'Xóa ảnh',
    labelButtonAbortItemLoad: 'Dừng',
    labelButtonRetryItemLoad: 'Thử lại',
    labelButtonAbortItemProcessing: 'Hủy',
    labelButtonRetryItemProcessing: 'Thử lại',
    labelButtonProcessItem: 'Tải lên',
    maxFiles: 5,
    acceptedFileTypes: 'image/jpeg, image/png',
  }

  constructor(
    public navCtrl: NavController, 
    public loading: LoadingProvider,
    public rest: RestProvider,
    public alert: AlertController,
    public global: GlobalProvider,
    ) {
      this.name = '';
      this.content = '';
      this.pondFiles = 0;
  }

  ionViewWillEnter(){
    let currentTime = new Date();
    this.timestamp = currentTime.getTime();
    this.loading.show()
    this.rest.initColorMix(this.timestamp).then(
      (scs) => {
        this.loading.hide();
      },
      (err) => {
        this.loading.hide();
        let initSessionError = this.alert.create({
          title: 'Lỗi',
          message: 'Không thể kết nối với máy chủ',
          buttons: [
            {
              text: 'OK',
              handler: () => {
                this.navCtrl.popToRoot();
              }
            }
          ]
        });
        initSessionError.present();
      }
    );
    let user_token = localStorage.getItem('token');
    this.myPond.pond.setOptions({
      server: {
        process: {
          url: this.rest.UPLOAD_COLOR_MIX_URL+'/process?token='+user_token+'&&timestamp='+this.timestamp,
          method: 'POST',
          withCredentials: false,
          onload: function(response) {
              return response;
          },
          onerror: function(response) {
              return response.data;
          }
        },
        revert: {
          url: this.rest.UPLOAD_COLOR_MIX_URL+'/revert?token='+user_token,
          method: 'DELETE'
        },
        restore: null,
        load: null,
      }
    });
  }

  pondHandleInit() {
    console.log('FilePond has initialised', this.myPond);
  }

  pondHandleAddFile() {
    this.pondFiles = this.myPond.getFiles().length;
  }

  pondHandleRemoveFile() {
    this.pondFiles = this.myPond.getFiles().length;
    
  }

  presentNotifications(myEvent) {
    console.log(myEvent);
    // let popover = this.popoverCtrl.create('NotificationPage');
    // popover.present({
    //   ev: myEvent
    // });
    this.navCtrl.push('NotificationPage');
  }

  clickUpload() {
    this.loading.show();
    this.rest.postColorMixId({name: this.name, content: this.content}, this.timestamp).then(
      (scs) => {
        let successUpload = this.alert.create({
          message: 'Yêu cầu đã được gửi lên máy chủ thành công.',
          buttons: [
            {
              text: 'OK',
              handler: () => {
                this.navCtrl.popToRoot();
              }
            }
          ]
        })
        this.loading.hide();
        this.name = '';
        this.content = '';
        successUpload.present();
      },
      (err) => {
        let errorUpload = this.alert.create({
          message: 'Có lỗi đã xảy ra! Xin hãy thử lại sau',
          buttons: [
            {
              text: 'OK'
            }
          ]
        })
        this.loading.hide();
        errorUpload.present();
      }
    );
  }

}



